* {
	box-sizing: border-box;
	margin: 0px;
	padding: 0px;
}

:root {
	/* --site-default-bg: linear-gradient(to right, #393131, 
	#503d40, 
	#3c383a, 
	#393131, 
	#b17f7f); */
	--site-default-bg: #393131;
	--site-default-semantic-bg: #282c34;
	--site-default-popover-bg: rgb(41, 78, 102);
	--site-default-input-bg: rgb(55, 54, 54);

	/* Цвета */
	--site-default-color: rgb(223, 210, 210);
	--site-active-color: rgb(223, 178, 30);
	--site-default-spinnerColor: #ae3a3a;
	--site-logo-color: #a62626;

	/* Размер шрифта */
	--site-big-fs: 2.2rem;
	--site-medium-fs: 1.5rem;
	--site-small-fs: 1.1rem;
	--site-smallest-fs: 0.8rem;

	/* Шрифт */
	--site-menu-ff: "Ubuntu";
	--site-header-ff: "Montserrat";
	--site-text-ff: "Roboto";

	/* Отступы */
	--site-small-pd: 1.5rem;
	--site-small-mr: 1rem;
	--site-medium-mr: 2rem;
	--site-big-mr: 3rem;

	
	--site-default-spinnerShadow: 2px 2px 7px #ed0b0b;

	--site-small-borderRadius: 10px;
}

h1,h2,h3,h4,h5,h6 {
	font-family: var(--site-header-ff);
}

body {
	background-color: var(--site-default-bg);
}

section {
	position: relative;
	min-height: 84vh;
}

